import ExtensionsHeader from "@components/extensions-header";
import { Fragment, useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import { ChevronDown, Edit } from "react-feather";
import { FormattedMessage } from "react-intl";
import { MoreVertical, Trash2 } from "react-feather";
import { injectIntl } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";
import { toast } from "react-toastify";
import {
  Col,
  Button,
  Card,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Row,
  UncontrolledDropdown,
} from "reactstrap";
import Swal from "sweetalert2";
import { getData, remove } from "../store/action";
import Sidebar from "./Sidebar";

const CustomHeader = ({ toggleSidebar }) => {
  return (
    <Fragment>
      <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-1 mb-75">
        <Row className="justify-content-end">
          <Col
            xl="6"
            className="d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
          >
            <div className="justify-content-end">
              <Button.Ripple color="primary" onClick={toggleSidebar}>
                <FormattedMessage id="Add" />
              </Button.Ripple>
            </div>
          </Col>
        </Row>
      </div>
    </Fragment>
  );
};

const CloudDemoList = ({ intl }) => {
  const dispatch = useDispatch();
  const store = useSelector((state) => state.cloudDemo);

  const [disable, setDisable] = useState(false);
  const [sidebarOpen, setSidebarOpen] = useState(false);

  const [loadData, setLoadData] = useState(false);
  const { id } = useParams();
  const lang = useSelector((state) => state.common.language);

  const columns = [
    {
      name: <FormattedMessage id="Image" />,
      minWidth: "50px",
      selector: "Image",
      sortable: false,
      cell: (row) => (
        <div class="avatar me-1" width="32" height="32">
          <img src={row?.image} alt={Image} width="32" height="32" />
        </div>
      ),
    },
    {
      name: <FormattedMessage id="Title" />,
      minWidth: "120px",
      selector: "short_description",
      sortable: false,
      cell: (row) => row?.short_description,
    },
    {
      name: <FormattedMessage id="Link Demo" />,
      minWidth: "120px",
      selector: "link_demo",
      sortable: false,
      cell: (row) => row?.demo_url,
    },
    {
      name: <FormattedMessage id="Link Sample" />,
      minWidth: "120px",
      selector: "sample_code",
      sortable: false,
      cell: (row) => row?.sample_code,
    },
    {
      name: <FormattedMessage id="actions" />,
      minWidth: "100px",
      cell: (row) => (
        <UncontrolledDropdown>
          <DropdownToggle tag="div" className="btn btn-sm">
            <MoreVertical size={14} className="cursor-pointer" />
          </DropdownToggle>
          <DropdownMenu right>
            <DropdownItem
              tag={Link}
              to={`/apps/categoryCloud/demo/edit/${row.id}`}
              className="w-100"
            >
              <Edit size={14} className="mr-50" />
              <span className="align-middle">
                <FormattedMessage id="edit" />
              </span>
            </DropdownItem>
            <DropdownItem
              tag={Link}
              to={"#"}
              className="w-100"
              onClick={() => {
                Swal.fire({
                  title: intl.formatMessage({
                    id: "Are you sure you want to delete?",
                  }),
                  icon: "warning",
                  showCancelButton: true,
                  confirmButtonColor: "#ea5455",
                  cancelButtonColor: "#3085d6",
                  confirmButtonText: intl.formatMessage({ id: "delete" }),
                  cancelButtonText: intl.formatMessage({ id: "Cancel" }),
                  reverseButtons: true,
                }).then((result) => {
                  if (result.isConfirmed) {
                    dispatch(remove({ id: parseInt(row.id) }));
                  }
                });
              }}
            >
              <Trash2 size={14} className="mr-50" />
              <span className="align-middle">
                <FormattedMessage id="delete" />
              </span>
            </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
      ),
    },
  ];

  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
    setDisable(false);
  };

  useEffect(() => {
    dispatch(
      getData({
        filter: {
          productId: parseInt(id),
          lang: lang,
        },
        skip: 0,
        limit: 100,
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, [lang, loadData]);

  useEffect(() => {
    switch (store?.type) {
      case "ADD_PRODUCT_DEMO":
        if (store?.status == 200) {
          toast.success(<FormattedMessage id={"successfully added new!"} />);
          setLoadData(!loadData);
          toggleSidebar();
        } else if (store?.status == 400) {
          toast.warn(<FormattedMessage id={"Add new failure!"} />);
        }

        break;
      case "UPDATE_PRODUCT_DEMO":
        if (store?.status == 200) {
          toast.success(<FormattedMessage id={"Update successful!"} />);
          setLoadData(!loadData);
        } else if (store?.status == 400) {
          toast.warn(<FormattedMessage id={"Update failed!"} />);
        }

        break;
      case "DELETE_PRODUCT_DEMO":
        if (store?.status == 200) {
          toast.success(<FormattedMessage id={"Delete successfully!"} />);
          setLoadData(!loadData);
        } else if (store?.status == 400) {
          toast.warn(<FormattedMessage id={"Delete failed!"} />);
        }

        break;

      default:
        break;
    }
  }, [store.status]);

  const dataToRender = () => {
    if (store?.data?.data?.length > 0) {
      return store?.data?.data;
    }
  };

  return (
    <div className="app-user-list">
      <ExtensionsHeader title={<FormattedMessage id="Demo" />} />

      <Card>
        <DataTable
          noHeader
          subHeader
          responsive
          noDataComponent={
            <div className="sc-fznWqX gnahTY">
              <div className="sc-AxjAm gIMaKV rdt_Table" role="table">
                <div className="sc-fzqARJ icdHOq">
                  <div
                    style={{
                      padding: "25px ",
                      textAlign: "center",
                      color: "black",
                      background: "white",
                    }}
                  >
                    <FormattedMessage id={"There are no records to display"} />
                  </div>
                </div>
              </div>
            </div>
          }
          paginationServer
          columns={columns}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          data={dataToRender()}
          subHeaderComponent={<CustomHeader toggleSidebar={toggleSidebar} />}
        />
      </Card>
      <Sidebar
        open={sidebarOpen}
        toggleSidebar={toggleSidebar}
        {...{ disable, setDisable }}
      />
    </div>
  );
};

export default injectIntl(CloudDemoList);
